.about-body-container {
  margin: 0 15px; }
.profile-logo {
  display: flex;
  justify-content: center;
  img {
    margin-top: 10px;
    padding: 10px;
    max-width: 300px; } }
.profile-title {
  display: block;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin: 1em auto;
  h1 {
    font-size: 3em;
    font-weight: 500;
    line-height: 1.25em; }
  h3 {
    font-size: 1.25em; } }
//
.about-text-container {
  display: block;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin: 1em auto;
  border-radius: 1em;
  max-width: 900px;
  h2 {
    font-size: 30px;
    padding: 10px 0px; }
  p {
    padding: 15px 0;
    font-size: 1.4em;
    a {
      font-weight: bold;
      color: #FE9920; } }
  ul {
    display: flex;
    flex-direction: row;
    justify-content: center;
    li {
      padding: 3px 5px;
      border-radius: 5px;
      margin: 5px;
      border: 2px solid $white;
      list-style: none;
      a {
        color: $white; } } } }
// Switch to mobile navigation
@media screen and (max-width: 1200px) {
  .profile-title {
    h1 {
      font-size: 2.5em; } }
  .about-text-container {
    p {
      font-size: 1.1em; } } }
