.banner-container {
  display: flex;
  flex-flow: column;
  padding: 3em 0;
  max-height: 100%;
  div {
    position: relative;
    width: 100%;
    margin-bottom: 3em;
    // background-image: linear-gradient(to right, rgba(0, 0, 0, 0.0) 10%, rgba(#FFEDD6, 0.1), rgba(0, 0, 0, 0.0) 90%)
    justify-content: center;
    a {
      display: flex;
      justify-content: center;
      align-items: center;
      .img-container {
        display: flex;
        justify-content: center;
        align-items: center;
        img {
          display: flex;
          width: 570px;
          max-height: 230px;
          max-width: 100%;
          justify-content: center;
          align-items: center;
          //
          transition: all 0.25s ease-in-out; } }
      p {
        position: absolute;
        font-family: $font-Viga;
        font-size: 2em;
        text-shadow: 2px 2px $bg-color;
        letter-spacing: 5px; } } }

  div:last-of-type {
    margin-bottom: 0; }
  div:hover {
    img {
      opacity: 0.5; }
    animation: bannertransition 0.5s ease-in-out; } }
@keyframes bannertransition {
  from {}

  to {} }
